import React from 'react';
import { connect } from 'react-redux';

class ScorePanel extends React.Component {
    render() {
        const { teamInfo: { point, foul, name } } = this.props;
        const foulClassName = foul >= 7 ? (foul >= 10 ? "foul teamfoul2" : "foul teamfoul1") : "foul";
        return (
            <div className="scorepanel">
                <div className="team">{name}</div>
                <div className="point">{point}</div>
                <div className={foulClassName}>{foul}</div>
            </div>
        );

    }
}

const mapStateToProps = ({ game: { team } }, ownProps) => {
    return { teamInfo: team[ownProps.team] };
};

export default connect(mapStateToProps, null)(ScorePanel);